<template>
    <div class="views navigation" data-enable="true">
        <!-- 助航选择 -->
        <div class="routes border-1px-b shadow">
            <div class="routes-back" @click="handlerBack">
                <mu-icon value="navigate_before" size="30"/>
            </div>
            <div class="point-group">
                <i class="point sn"></i>
                <i class="point"></i>
                <i class="point"></i>
                <i class="point"></i>
                <i class="point en"></i>
            </div>
            <div class="route-group">
                <div class="route sn no-wrap" @click="handlerRoute('route-sn')">
                    <span :class="{p: !sn.name}">
                        {{sn.name || sn.p}}
                    </span>
                </div>
                <div class="route en no-wrap" @click="handlerRoute('route-en')">
                    <span :class="{p: !en.name}">
                        {{en.name || en.p}}
                    </span>
                </div>
                <img class="route-reverse" :src="reverseIcon" @click="reverse">
            </div>
        </div>

        <!-- 助航路线的历史纪录 -->
        <transition name="views-down">
            <div class="routes-wrapper" v-if="visible">
                <routes @change="change"/>
            </div>
        </transition>


        <transition name="up-move">
            <!-- navigation panel start -->
            <div class="navigation-panel" v-if="routes.length > 0">
                <div class="navigation-panel-info">
                    <div class="item" v-for="(item, index) in routes"
                         :class="{selected: id === item.attributes.ObjectID}"
                         @click="handlerToggleNavigation(item)">
                        <div class="type">{{index === 0 ? '推荐航线' : '备选航线'}}</div>
                        <div class="info">
                            <div class="left">
                                {{item.attributes.Shape_Length | formatDistance}}<br/>
                                {{item.attributes.Shape_Length > 1000 ? '公里' : '米'}}
                            </div>
                            <div class="right">
                                <p class="channel">{{item.attributes.Bridge_Number}} 座桥梁</p>
                                <p class="channel">{{item.attributes.ShipLock_Number}} 座船闸</p>
                                <p class="time">
                                    {{item.attributes.Total_TravelTime * 60 | secondToDate}}
                                </p>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="navigation-panel-button">
                    <mu-button class="button" @click="guide" round flat>模拟助航</mu-button>
                    <mu-button class="button" @click="start" round flat>开始助航</mu-button>
                </div>
            </div>
            <!-- navigation panel end -->
        </transition>


        <!-- 助航的路由 -->
        <transition name="views-left">
            <keep-alive>
                <router-view class="views"/>
            </keep-alive>
        </transition>
        <!-- 助航的路由 -->
    </div>
</template>

<script>
    import routes from './history'
    import {routeMixins, userMixins} from "../../common/mixins"
    import {getMapLocation} from "../../common/js/cordova/map"
    import {getNavigationRoute} from "../../api/navigation"
    import {debounce} from "../../types/index"
    import {secondToDate} from '../../common/utils/time'

    export default {
        name: "navigation",
        mixins: [userMixins, routeMixins],
        data() {
            return {
                id: '',
                routes: [],
                reverseIcon: 'static/icon/navigation_line.png',
                speed: 0,
                draft: 0,
                pureHeight: 0
            }
        },
        computed: {
            visible() {
                return !(this.sn.name && this.en.name)
            }
        },
        created() {
            const plan = debounce(() => {
                this.planRoutes()
            }, 500)

            this.$watch('sail', plan, {deep: true})
        },
        activated() {
            if (JSON.stringify(this.$route.query) === '{}') {
                this.speed = 0
                this.draft = 0
                this.pureHeight = 0

                return
            }

            this.speed = this.$route.query.speed
            this.draft = this.$route.query.draft
            this.pureHeight = this.$route.query.pureHeight

            if (!this.sail) {
                this.routes = []
            }
        },
        methods: {
            // 更改助航线路
            change(item) {
                this.setRouteSn(item.sn)
                this.setRouteEn(item.en)

                const route = JSON.stringify({
                    sn: item.sn,
                    en: item.en
                })

                this.saveRouteHistory(route)
            },
            // 点击返回
            handlerBack() {
                window.Arcgis && window.Arcgis.cancelPathPlanning('')

                this.resetRoutes()

                this.$router.back()
            },
            // 点击进入助航地点选择
            handlerRoute(name) {
                this.$router.push({name})
            },
            // 点击切换
            reverse() {
                this.reverseRoutes()
            },
            // 点击切换当前方案 &*
            handlerToggleNavigation(item, index) {
                this.$toast({
                    position: 'center',
                    type: 'loading',
                    message: '请等待 ...',
                    duration: 0
                })

                window.Arcgis && window.Arcgis.changeRoute(Number.parseInt(item.attributes.ObjectID), res => {
                    this.id = item.attributes.ObjectID

                    this.$toast.clear()

                    console.log('%c—————— 切换路线方案 ——————', 'color: #2196f3', item)
                })
            },
            // 点击开始助航
            start() {
                if (!this.isLogin) {
                    this.$toast({
                        type: 'fail',
                        position: 'center',
                        message: '您需要登录才能进行助航！'
                    })

                    return
                }

                this.$toast({
                    type: 'loading',
                    position: 'center',
                    message: '正在规划线路, 请等待 ...',
                    duration: 0
                })

                const route = JSON.stringify({
                    sn: this.sn,
                    en: this.en
                })

                this.saveRouteHistory(route)

                this.routes = []

                window.Arcgis && window.Arcgis.realGuide(this.id, res => {
                    try {
                        res = JSON.parse(res)
                    } catch (e) {
                        console.log(e)

                        res = {
                            speed: 0,
                            polar: 0,
                            distance: 0,
                            word: ''
                        }
                    }

                    this.$toast.clear()

                    this.$router.replace({
                        name: 'sail',
                        params: {
                            speed: res.speed,
                            polar: res.polar,
                            distance: res.distance,
                            word: res.word
                        }
                    })
                }, err => {
                    this.$toast.clear()
                })
            },
            guide() {
                this.$toast({
                    type: 'loading',
                    position: 'center',
                    message: '正在规划线路, 请等待 ...',
                    duration: 0
                })

                const route = JSON.stringify({
                    sn: this.sn,
                    en: this.en
                })

                this.saveRouteHistory(route)

                this.routes = []

                window.Arcgis && window.Arcgis.navigationStarting(this.id, res => {
                    try {
                        res = JSON.parse(res)
                    } catch (e) {
                        console.log(e)

                        res = {
                            speed: 0,
                            polar: 0,
                            distance: 0,
                            word: ''
                        }
                    }

                    this.$toast.clear()

                    this.$router.replace({
                        name: 'sail',
                        params: {
                            speed: res.speed,
                            polar: res.polar,
                            distance: res.distance,
                            word: res.word
                        }
                    })
                }, err => {
                    this.$toast.clear()
                })
            },
            // 路线规划
            async planRoutes() {
                let sn = this.sn, en = this.en, point

                // 如果没有添加任何线路，则不进行线路规划
                if (!sn.name || !en.name) return

                // 如果起始位置为 我的位置 ，获取 经度/longitude&纬度/latitude 信息
                if (sn.name === '我的位置') {
                    point = await getMapLocation()

                    sn = Object.assign({}, sn, {longitude: point.x, latitude: point.y})
                }

                // 如果结束位置为 我的位置 ，获取 经度/longitude&纬度/latitude 信息
                if (en.name === '我的位置') {
                    point = await getMapLocation()

                    en = Object.assign({}, en, {longitude: point.x, latitude: point.y})
                }

                console.log(`起始点位信息: `, sn)
                console.log(`结束点位信息: `, en)

                // 获取线路
                this.getRoutes(sn, en)
            },
            // 获取路线
            getRoutes(sn, en) {
                const data = {
                    startCoordinate: {
                        longitude: sn.longitude,
                        latitude: sn.latitude
                    },
                    goalCoordinate: {
                        longitude: en.longitude,
                        latitude: en.latitude
                    },
                    vehicle: {
                        vehicleID: "Passenger",
                        deadWeightTonnage: 0,
                        lengthOverall: 0,
                        mouldedBreadth: 0,
                        mouldedDepth: 0,
                        loadedDraft: this.draft,
                        freeboardHeight: this.pureHeight
                    },
                    averageSpeed: this.speed
                }

                console.log(data, '正在获取助航线路 ...')

                getNavigationRoute(data).then(res => {
                    if (!res) return

                    this.routes = res.routes.features

                    this.id = this.routes[0].attributes.ObjectID

                    window.Arcgis &&
                    window.Arcgis.pathPlanning(res, [sn, en], console.log)
                }).catch(console.log)
            }
        },
        filters: {
            secondToDate: secondToDate
        },
        components: {
            routes
        }
    }
</script>

<style lang="stylus">
    @import "../../common/stylus/mixins.styl"

    .navigation
        overflow hidden
        .routes
            position absolute
            left 0
            right 0
            top 0
            z-index 100
            display flex
            align-items center
            padding 10px 13px 10px
            height 110px
            background-color rgba(255, 255, 255, .9)
            .routes-back
                margin-top 10px
                margin-right 10px
                align-self flex-start
            .point-group
                position relative
                display flex
                flex-direction column
                align-items center
                justify-content space-around
                width 38px
                height 80px
                background-color #F9F9F9
                border-radius 10px 0 0 10px
                .point
                    display block
                    width 0
                    height 0
                    border 2px solid #DDDDDD
                    border-radius 50%
                    &.sn
                        margin-top 15px
                        border 4px solid #0DC56E
                    &.en
                        margin-bottom 15px
                        border 4px solid #F03737
            .route-group
                flex 1
                padding-right 60px
                background-color #F9F9F9
                overflow: hidden
                border-radius 0 10px 10px 0
                .route
                    position relative
                    width 100%
                    height 40px
                    line-height 40px
                    &:first-child:before
                        bottom-line(#E8E8E8)
                    span
                        color #212121
                        font-size 14px
                    .p
                        color #BBB
                .route-reverse
                    position absolute
                    top 50%
                    right 30px
                    width 25px
                    height 25px
                    transform translate3d(0, -50%, 0)
        .routes-wrapper
            position absolute
            top 0
            left 0
            padding-top 110px
            width 100%
            height 100%
            background-color #efefef

        .navigation-panel
            position absolute
            bottom 0
            left 0
            right 0
            z-index 100
            background-color rgba(255, 255, 255, .9)
            .navigation-panel-info
                display flex
                border-bottom 1px solid #F8F8F8
                .item
                    flex 1
                    padding 10px
                    border-right .5px solid #FAFAFA
                    transition color .5s
                    &.selected
                        color #2E7BFF
                    &:last-child
                        border-right none
                    .type
                        margin-bottom 5px
                        text-align center
                    .info
                        display flex
                        align-items center
                        justify-content center
                        .left
                            margin-right 10px
                            line-height 1.25
                            font-size $font-size-lm
                            text-align center
                        p
                            margin-top 4px
                            font-size $font-size-sl
            .navigation-panel-button
                padding 13px 0
                text-align center
                .button
                    padding 10px
                    color #F4F8FF
                    background-color #4287FF
                    font-size $font-size-ml
                    border-radius 100px
                    &:last-child
                        margin-left 15px

</style>



