<template>
    <div class="navigation-routes-history">
        <div class="scroll-wrapper" v-if="routes.length">
            <mu-list>
                <swipe-cell :right-width="88" v-for="(item, index) in routes" :key="index">
                    <mu-list-item class="border-1px-b" @click.native="handlerItem(item)">
                        <mu-list-item-content style="display: flex; align-items: center; line-height: 1.5em">
                            <span class="no-wrap" style="display: inline-block; width: 45%; max-height: 3em; color: #2e7bff;">{{item.sn.name}}</span>
                            <span style="margin: 0 0.5em;">-</span>
                            <span class="no-wrap" style="display: inline-block; width: 45%; max-height: 3em; color: #2e7bff;">{{item.en.name}}</span>
                        </mu-list-item-content>
                        <mu-list-item-action>
                            <mu-button color="#2E7BFF" icon>
                                <mu-icon size="16" value="near_me"/>
                            </mu-button>
                        </mu-list-item-action>
                    </mu-list-item>
                    <div style="width: 88px; height: 100%" slot="right">
                        <mu-button style="height: 100%; width: 100%; color: #fff; background-color: #f44336"
                                   @click="deleteRoute(item)"
                                   flat>删除
                        </mu-button>
                    </div>
                </swipe-cell>
            </mu-list>
            <div class="clear-button-wrapper">
                <mu-button class="button" @click="handlerClear" flat>清空历史纪录</mu-button>
            </div>
        </div>
        <no-success text="还没有留下历史数据" v-else/>
    </div>
</template>

<script>
    import {routeMixins} from "../../common/mixins"
    import NoSuccess from "../../components/no-success/no-success"
    import SwipeCell from 'vant/lib/swipe-cell'
    import 'vant/lib/swipe-cell/style'

    export default {
        name: "navigation-history",
        mixins: [routeMixins],
        computed: {
            routes() {
                return this.history.map(item => {
                    let route
                    try {
                        route = JSON.parse(item)
                    } catch (e) {
                        console.log(e)
                        route = {
                            sn: {},
                            en: {}
                        }
                    }

                    return route
                })
            }
        },
        methods: {
            handlerItem(item) {
                this.$emit('change', item)
            },
            handlerClear() {
                this.clearRouteHistory()
            },
            deleteRoute(item) {
                this.deleteRouteHistory(item)
            }
        },
        components: {
            SwipeCell,
            NoSuccess
        }
    }
</script>

<style lang="stylus" scoped>
    @import "../../common/stylus/variable.styl"

    .navigation-routes-history
        width 100%
        height 100%
        background-color #efefef
        .scroll-wrapper
            width 100%
            height 100%
            >>> .mu-item
                background-color #fff
            >>> .mu-item-content
                font-size $font-size-ms
            .clear-button-wrapper
                display flex
                background-color #fff
                > .button
                    width 100%
                    height 47px
</style>



